*{
    font-family: "Poppins", sans-serif;
    transition: 0.2s;
}

.homepage-header {
    margin: 30px 20px;
}

.top-margin {
    margin-top: 100px;
}

.homepage-header p {
    font-size: 22px;
    color: rgb(61, 61, 61);
    font-weight: 600;
    margin: 5px 0;
}

.homepage-header div {
    background-color: var(--edificeGreen);
    height: 7px;
    width: 100px;
    border-radius: 20px;
}

.homepage-course-cards-list {
    display: flex;
    flex-wrap: wrap;
    overflow-x: auto;
    overflow-y: hidden;
}

.homepage-course-provider-list button{
    background: white;
    color: var(--edificeGreen);
    padding: 1rem 4rem;
    margin: 1rem;
    border: 1px solid var(--edificeGreen);
    border-radius: 2rem;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
}

.homepage-course-provider-list button:hover{
    transform: scale(1.05);
    background: var(--edificeGreen);
    color: white;
}

.loading-spin-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 5%;
}

section {
    margin: 5px;
    margin-bottom: 10px;
    background: var(--lightGreen);
    border-radius: 1rem;
    padding-top: 10px;
}

section .course-card {
    background: white;
}

section:nth-child(3) {
    background: rgba(220, 238, 255, 0.4);
    padding-bottom: 2rem;
}

section:nth-child(3) .course-card{
    background: var(--lightGreen);
}

@media (max-width: 1000px) {
    .homepage-course-cards-list {
        justify-content: center;
    }
}
