@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
  font-family: "Poppins", "Montserrat", "Gilroy", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 4px;
  height: 10px;
}

::-webkit-scrollbar-track {
  border-radius: 0;
  background: var(--courseCardBackground);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: var(--midGreen);
}

::-webkit-scrollbar-thumb:active {
  border-radius: 0;
  background: var(--edificeGreen);
}
:root {
    --edificeGreen: #00bc4e;
    --lightGreen: #f6fdf8;
    --midGreen: #00bc4e33;
    --courseCardImage: rgb(155, 232, 188);
    --white: #ffffff;
    --errorRed: #cc2828;
}

input {
    background: #f6fdf8;
    background: var(--lightGreen);
    padding: 0.7rem;
    font-size: 1rem;
    margin-bottom: 1rem;
    border-radius: 10px;
    border-style: solid;
    border-color: transparent;
    outline: none;
    color: #00bc4e;
    color: var(--edificeGreen);
}

input:focus {
    border-color: #00bc4e;
    border-color: var(--edificeGreen);
}
*{
    font-family: "Poppins", sans-serif;
    transition: 0.2s;
}

.homepage-header {
    margin: 30px 20px;
}

.top-margin {
    margin-top: 100px;
}

.homepage-header p {
    font-size: 22px;
    color: rgb(61, 61, 61);
    font-weight: 600;
    margin: 5px 0;
}

.homepage-header div {
    background-color: var(--edificeGreen);
    height: 7px;
    width: 100px;
    border-radius: 20px;
}

.homepage-course-cards-list {
    display: flex;
    flex-wrap: wrap;
    overflow-x: auto;
    overflow-y: hidden;
}

.homepage-course-provider-list button{
    background: white;
    color: var(--edificeGreen);
    padding: 1rem 4rem;
    margin: 1rem;
    border: 1px solid var(--edificeGreen);
    border-radius: 2rem;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
}

.homepage-course-provider-list button:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background: var(--edificeGreen);
    color: white;
}

.loading-spin-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 5%;
}

section {
    margin: 5px;
    margin-bottom: 10px;
    background: var(--lightGreen);
    border-radius: 1rem;
    padding-top: 10px;
}

section .course-card {
    background: white;
}

section:nth-child(3) {
    background: rgba(220, 238, 255, 0.4);
    padding-bottom: 2rem;
}

section:nth-child(3) .course-card{
    background: var(--lightGreen);
}

@media (max-width: 1000px) {
    .homepage-course-cards-list {
        justify-content: center;
    }
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 25px;
    position: fixed;
    width: 100vw;
    max-height: 100px;
    background: white;
    top: 0;
    z-index: 2000;
}

.navbar-left-portion {
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: 0.2s;
}

.navbar-left-portion:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.navbar-left-portion:active{
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}

.navbar-left-portion img {
    height: 50px;
    padding-right: 0.5rem;
}

.navbar-left-portion p {
    color: var(--edificeGreen);
    font-size: 1.7rem;
}

.navbar-right-portion p {
    background: var(--edificeGreen);
    background-color: var(--edificeGreen);
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    transition: 0.2s;
}

.navbar-right-portion p:hover{
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    background-color: rgb(241, 75, 75);
}

.course-card {
    cursor: pointer;
    background-color: var(--lightGreen);
    border-radius: 10px;
    margin: 10px;
    padding: 15px;
    max-width: 200px;
    min-width: 200px;
    transition: 0.2s;
}

.course-card:hover {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
    box-shadow: 0px 1px 10px rgb(238, 238, 238);
}

.course-card:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
    box-shadow: none;
    background: var(--midGreen);
}

.course-card div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.course-card p:nth-child(2) {
    font-weight: 500;
    max-width: 200px;
    text-overflow: ellipsis;
    font-weight: 600;
}

.course-card p:nth-child(3) {
    font-weight: 500;
    font-size: 0.8rem;
    font-weight: bold;
    max-width: 200px;
    text-overflow: ellipsis;
}

.thumbnail {
    overflow: hidden;
    border-radius: 10px;
    width: 110px;
    height: 150px;
    min-width: 110px;
    min-height: 150px;
    background-color: var(--edificeGreen);
}

.thumbnail img {
    width: 100%;
    background-size: cover;
}

